// @import '~node_modules/modern-normalize/modern-normalize.css';

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
  box-sizing: border-box;
}

body {
  &.no-scroll {
    overflow: hidden;
  }
  height: 100%;

  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
  overflow-y: hidden;
}

::-webkit-scrollbar {
  width: 10px;
  height: 20px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(36, 253, 239);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(180deg, #4c00fe 0%, #dc56c5 99%);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background-image: linear-gradient(180deg, #dc56c5 0%, #4c00fe 99%);
}

.main {
  flex: 1 1 auto;
}

.wrapper-content {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.link {
  text-decoration: none;
}
.list {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
p,
ul {
  margin: 0px;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
