.preloader {
  position: fixed;
  opacity: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(2, 0, 36);
  background: radial-gradient(
    circle,
    rgba(2, 0, 36, 1) 0%,
    rgba(76, 9, 121, 1) 50%,
    rgba(0, 36, 43, 1) 100%
  );
  background-position: center;
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ishidden {
  display: none;
  visibility: hidden;
}
.wrapper {
  position: absolute;
  width: 320px;
  @include media(tablet) {
    max-width: 768px;
  }

  @include media(desktop) {
    max-width: 1280px;
  }
  //   top: 50%;
  //   transform: translate(0, -40%);
  //   display: block;
  //   margin-left: auto;
  //   margin-right: auto
}
