.line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 2px;
  height: 8px;
  background-color: red;
}

.circle {
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: blue;
}
.circle-top {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  left: -3.5px;
}
.circle-bottom {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  left: -3.5px;
}

.dot {
  position: absolute;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  left: -1.5px;
}

