@-webkit-keyframes line {
  0% {
    height: 4px;
    top: 0px;
    left: 0px;
    -webkit-transform: rotate(-65deg);
  }
  10% {
    height: 220px;
    top: -110px;
    left: 15px;
  }
  45% {
    height: 200px;
    top: -100px;
    left: 25px;
  }
  70% {
    height: 8px;
    top: 0px;
    left: 25px;
    -webkit-transform: rotate(0deg);
  }
  100% {
    height: 8px;
    top: 0px;
    left: 15px;
    -webkit-transform: rotate(0deg);
  }
}
@-webkit-keyframes dot-top {
  0% {
